/***/ "./node_modules/json-loader/index.js!./.cache/json/miscellaneous-freecodecamp-algorithm-merge-sort-guide.json":
/***/ (function(module, exports) {

	module.exports = {"data":{"markdownRemark":{"html":"<p>Most modern languages have an inbuilt sort() function which automatically sorts an input array or list. Did you ever wonder how the sort function actually works on the inside?. Knowing common sorting algorithms and their implementations is the most important part of a coding interview. In this series of articles, we will look at several important sorting algorithms. How they are implemented, the time and space complexity etc. Our very first post is on Merge Sort.</p>\n<p>To learn about Merge Sort, a basic knowledge about <a href='http://programmers.stackexchange.com/questions/25052/in-plain-english-what-is-recursion' target='_blank' rel='nofollow'>Recursion</a> is a pre-requisite. Merge Sort is based on the principle of Divide and Conquer. The whole process of sorting an array of N integers can be summarized into three steps-</p>\n<ul>\n<li>Divide the array into two halves.</li>\n<li>Sort the left half and the right half using the same recurring algorithm.</li>\n<li>Merge the sorted halves.</li>\n</ul>\n<p>The biggest advantage of using Merge sort is that the <a href='https://www.youtube.com/watch?v=V42FBiohc6c&list=PL2_aWCzGMAwI9HK8YPVBjElbLbI3ufctn' target='_blank' rel='nofollow'>time complexity</a> is only n*log(n) to sort an entire Array. It is a lot better than n^2 running time of bubble sort or insertion sort.  </p>\n<p>Before we write code, let us understand how merge sort works with the help of a diagram.</p>\n<p><img src=\"//discourse-user-assets.s3.amazonaws.com/original/2X/4/4712ef1a5d856dbb4af393fcc08a820a38787395.png\" alt=\"Merge Sort\"></p>\n<ul>\n<li>Initially we have an array of 6 unsorted integers Arr(5, 8, 3, 9, 1, 2)</li>\n<li>We split the array into two halves Arr1 = (5, 8, 3) and Arr2 = (9, 1, 2).</li>\n<li>Again, we divide them into two halves: Arr3 = (5, 8) and Arr4 = (3) and Arr5 = (9, 1) and Arr6 = (2)</li>\n<li>Again, we divide them into two halves: Arr7 = (5), Arr8 = (8), Arr9 = (9), Arr10 = (1) and Arr6 = (2)</li>\n<li>We will now compare the elements in these sub arrays in order to merge them.</li>\n</ul>\n<h2>Implementation</h2>\n<h3>C++ Implementation</h3>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>void merge(int array[], int left, int mid, int right)\n{\n    int i, j, k;\n\n    // Size of left sublist\nint size_left = mid - left + 1;\n\n// Size of right sublist\nint size_right =  right - mid;\n\n/* create temp arrays */\nint Left[size_left], Right[size_right];\n\n/* Copy data to temp arrays L[] and R[] */\nfor(i = 0; i < size_left; i++)\n{\n    Left[i] = array[left+i];\n}\n\nfor(j = 0; j < size_right; j++)\n{\n    Right[j] = array[mid+1+j];\n}\n\n// Merge the temp arrays back into arr[left..right]\ni = 0; // Initial index of left subarray\nj = 0; // Initial index of right subarray\nk = left; // Initial index of merged subarray\n\nwhile (i < size_left && j < size_right)\n{\n    if (Left[i] <= Right[j])\n    {\n        array[k] = Left[i];\n        i++;\n    }\n    else\n    {\n        array[k] = Right[j];\n        j++;\n    }\n    k++;\n}\n\n// Copy the remaining elements of Left[]\nwhile (i < size_left)\n{\n    array[k] = Left[i];\n    i++;\n    k++;\n}\n\n// Copy the rest elements of R[]\nwhile (j < size_right)\n{\n    array[k] = Right[j];\n    j++;\n    k++;\n}\n}\n\nvoid mergeSort(int array[], int left, int right)\n{\n    if(left < right)\n    {\n        int mid = (left+right)/2;\n\n        // Sort first and second halves\n    mergeSort(array, left, mid);\n    mergeSort(array, mid+1, right);\n\n    // Finally merge them\n    merge(array, left, mid, right);\n}\n}</code></pre>\n      </div>\n<p><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/rocket.png?v=3\" title=\":rocket:\" alt=\":rocket:\" class=\"forum-image\"> <a href='https://repl.it/CYVc/1' target='_blank' rel='nofollow'>Run Code</a> </p>\n<h3>Javascript Implementation</h3>\n<p>Let’s write MergeSort in JavaScript:  </p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>function mergeSort (arr) {\n  if (arr.length < 2) return arr;\n  var mid = Math.floor(arr.length /2);\n  var subLeft = mergeSort(arr.slice(0,mid));\n  var subRight = mergeSort(arr.slice(mid));\n  return merge(subLeft, subRight);\n}</code></pre>\n      </div>\n<p>First we check the length of the array. If it is 1 then we simply return the array. This would be our base case. Else, we will find out the middle value and divide the array into two halves. We will now sort both of the halves with recursive calls to MergeSort function.  </p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>function merge (a,b) {\n    var result = [];\n    while (a.length >0 && b.length >0)\n        result.push(a[0] < b[0]? a.shift() : b.shift());\n    return result.concat(a.length? a : b);\n}</code></pre>\n      </div>\n<p>When we merge the two halfs, we store the result in an auxilliary array. We will compare the starting element of left array to the starting element of right array. Whichever is lesser will be pushed into the results array and we will remove it from there respective arrays using [shift() operator</a>. If we still end up with values in either of left or right array, we would simply concatenate it in the end of the result. Here is the sorted result:  </p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>var test = [5,6,7,3,1,3,15];\nconsole.log(mergeSort(test));\n\n>> [1, 3, 3, 5, 6, 7, 15]</code></pre>\n      </div>\n<p><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/rocket.png?v=3\" title=\":rocket:\" alt=\":rocket:\" class=\"forum-image\"> <a href='https://repl.it/CYVd' target='_blank' rel='nofollow'>Run Code</a></p>\n<p>If you still have problem in understanding MergeSort, a <a href='https://www.youtube.com/watch?v=TzeBrDU-JaY' target='_blank' rel='nofollow'>video explanation</a> will make it even more clear.</p>","fields":{"slug":"/miscellaneous/freecodecamp-algorithm-merge-sort-guide/"},"frontmatter":{"title":"Freecodecamp Algorithm Merge Sort Guide"}}},"pathContext":{"slug":"/miscellaneous/freecodecamp-algorithm-merge-sort-guide/"}}

/***/ })

